@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
}
body {
  color: #222222;
  font-family: 'Poppins', sans-serif, Arial, Helvetica, sans-serif;
}

h1 {
  margin-bottom: 3em;
  text-align: center;
  font-weight: 100;
}

.mainSection {
  padding: 3em 1em;
}

@media (min-width: 600px) {
  .mainSection {
    padding: 3em 6em;
  }
}
